import React, { Component } from 'react';
import { Slider, Tag } from 'antd';

export default class TemporalFilter extends Component {
    constructor(props) {
        super(props);
    }

    render() {

        let { subValueListTemp, subValueType } = this.props;

        if (subValueType) {
            let marks = {};
            for (let i = 0; i < subValueListTemp.length; i++) {
                if (i === 0 || i === (subValueListTemp.length - 1)) {
                    marks[parseInt(i / (subValueListTemp.length - 1) * 100)] = {
                        label: <span className={i === 0 ? "timeMarkMin" : "timeMarkMax"} >{subValueListTemp[i].split("/")[0]}</span>,
                    }
                }

            } return (
                <div className="TemporalFilter1">
                    <div className="TemporalFilterContent">
                        <div className="topicDetails1">
                            <Slider range marks={marks} defaultValue={[0, 100]} />
                        </div>
                    </div>
                </div>

            )
        }
        else return null;



    }

}