import React, { Component } from "react";
import { Drawer}from 'antd';
// import FactView from '@/view/FactView';
import './MobileTab.less';
import Draggable from 'react-draggable';
import AlgorithmSettings from "../FactListViewNew/AlgorithmSettings";
import TopicSettings from "../FactListViewNew/TopicSettings";
import RightBottomButton from "../FactListViewNew/RightBottomButton";
import MoreFactsSelections from "../FactListViewNew/MoreFactsSelections";
import RfactsSettings from "../FactListViewNew/RfactsSettings";
import ConfigureChart from "../../../view/FactEditorNew/Fact/ConfigureChart";



export default class MobileTab extends Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
        }
    }

    Settings = () => {
        this.setState({
            visible: true,
            contentType: 'Settings'
        });
        if (this.state.visible && this.state.contentType === 'Settings') {
            this.setState({
                visible: false
            });
        }

    }
    Configuration = () => {
        this.setState({
            visible: true,
            contentType: 'Configuration'
        });
        if (this.state.visible && this.state.contentType === 'Configuration') {
            this.setState({
                visible: false
            });
        }
    }
    DataFacts = () => {
        this.setState({
            visible: true,
            contentType: 'DataFacts'
        });
        if (this.state.visible && this.state.contentType === 'DataFacts') {
            this.setState({
                visible: false
            });
        }
    }
    onClose = () => {
        this.setState({
            visible: false,
        });
    };

   
    render() {
        const { visible, contentType } = this.state
        const { intl, initDone} = this.props

        let DrawContentView;
        switch (contentType) {
            case 'Settings':
                DrawContentView = <div className="MobileTabSettings" style={{width:"290px",margin:"auto"}}>
                    <AlgorithmSettings {...this.props}/>
                    <TopicSettings {...this.props}/>
                    <RightBottomButton {...this.props}/>
                </div>
                break;
            case 'Configuration':
                DrawContentView = <div style={{ width: "290px",margin:"auto" }}>
                   <ConfigureChart editingFact={this.props.editingCurrentFact} {...this.props}/>
                </div>
                break;
            case 'DataFacts':
                if(!this.props.editstate){
                    DrawContentView = <div style={{width:"290px",margin:"auto"}}>
                    <MoreFactsSelections {...this.props}/>
                </div>
                }
                else  {

                    DrawContentView = <div style={{width:"290px",margin:"auto"}}>
                    <RfactsSettings  editingFact={this.props.editingCurrentFact}{...this.props}/>
                </div>
                }
                break;
            default:
                break;
        }

        return (
            <div className='mobileTabWrapper'>
                <Drawer
                    placement={'bottom'}
                    onClose={this.onClose}
                    visible={visible}
                    height={'auto'}
                    className="bottomDrawers"
                >
                    {DrawContentView}
                </Drawer>
                <div onClick={this.Settings} className='columnWrapper SettingsIcon'>
                    <div ></div>
                    <span>{initDone && intl.get("Settings")}</span>
                </div>
                <div onClick={this.props.editstate?this.Configuration:null} className={this.props.editstate?'columnWrapper ConfigurationIcon':'columnWrapper ConfigurationDisabledIcon'}>
                    <div ></div>
                    <span>{initDone && intl.get("Chart Configuration")}</span>
                </div>
                <div onClick={this.DataFacts} className='columnWrapper DatafactsIcon'>
                    <div ></div>
                    <span>{initDone && intl.get("Datafacts")}</span>
                </div>
            </div >
        )
    }
}