import React, { Component } from 'react';
import { Slider, Tag } from 'antd';
const { CheckableTag } = Tag;

export default class CategoricalFilter extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        let { subValueListTemp, subValueType } = this.props;

        if (subValueType) {
            
            return (
                <div className="CategoricalFilter1">
                    <div className="topicDetails1"style={{marginLeft: '5px'}}>
                        {subValueListTemp.map(tag => (
                            <CheckableTag
                                key={tag}
                                checked={subValueListTemp.indexOf(tag) > -1}

                            // checked={selectedTags.indexOf(tag) > -1}
                            // onChange={(checked) => {
                            //     let nextSelectedTags = checked ? [...selectedTags, tag] : selectedTags.filter(t => t !== tag);
                            //     setSelectedTags(nextSelectedTags);
                            // }}
                            >
                                {tag}
                            </CheckableTag>
                        ))}
                    </div>

                </div>

            )
        }
        else return null;

    }
}